import Vue from 'vue'
import VueRouter from 'vue-router'
import App from './App'

Vue.use(VueRouter)

import home from './components/home'
import pageCover from './pages/pageCover'
import pageMajiongEmpire from './pages/pageMajiongEmpire'
const Page404 = {
  template: '<div>该页面暂时无法提供，敬请期待！</div>'
}

const routes = [
  {
    path: '/',
    redirect: '/index'
  },
  {
    path: '/',
    component: home,
    children: [
      {
        path: '/index',
        name: 'index',
        component: pageCover
      }, // 入口页
      {
        path: '/gameMajiongEmpire',
        name: 'gameMajiongEmpire',
        component: pageMajiongEmpire
      }
    ]
  },
  {
    path: '/404',
    component: Page404
  }
]

const router = new VueRouter({
  routes
})

router.afterEach((to, from) => {
  // console.log(to, from)
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  template: '<App/>',
  components: { App },
  router
})



