<template>
  <section class="bed-home">
    <div class="bed-menu">
      <div class="box-menu">
        <div class="box-menu-item" v-for="item in menu" :key="item.id">
          <div class="menu-item" :class="{ 'active': item.id === $route.name }" @mouseover="handleHover(item.id)" @mouseout="handleHoverOut(item.id)" @click="handleMenuClick(item)">
            {{ item.name }}
            <div class="btn" v-show="item.withBtn && hoverId === item.id" @click="goUrl(item)">GO</div>
          </div>
          <div class="sub-menu" :class="{ 'active': sitem.id === $route.name }" v-for="sitem in item.nodes" :key="sitem.id" @mouseover="handleHover(sitem.id)" @mouseout="handleHoverOut(sitem.id)" @click="handleMenuClick(sitem)">
            {{ sitem.name }}
            <div class="btn" v-show="sitem.withBtn && hoverId === sitem.id" @click="goUrl(sitem)">GO</div>
          </div>
        </div>
      </div>
    </div>
    <div class="bed-content">
      <router-view></router-view>
    </div>
  </section>
</template>

<script>
  export default {
    name: 'home',
    data () {
      return {
        hoverId: '',   // 目前hover的id
        menu: [
          {
            id: 'index',
            name: '介绍',
            url: {
              path: '/index'
            },  // router的url
            withBtn: false,
            targetUrl: '',  // 直接访问的链接
            nodes: []   // 子菜单
          },
          {
            id: 'test',
            name: '测试',
            url: '',  // router的url
            withBtn: false,
            targetUrl: '',  // 直接访问的链接
            nodes: [
              {
                id: 'test1',
                name: '测试1 假如名称真的很长是未欧文奥未 未',
                url: {
                  path: '/test1'
                },  // router的url
                withBtn: false,
                targetUrl: '',  // 直接访问的链接
                nodes: []
              }
            ]   // 子菜单
          },
          {
            id: 'games',
            name: '游戏作品',
            url: '',
            withBtn: false,
            targetUrl: '',
            nodes: [
              {
                id: 'majiongEmpire',
                name: 'Majiong Empire on Web',
                url: {
                  name: 'gameMajiongEmpire'
                },
                withBtn: true,
                targetUrl: 'https://youngbeen.github.io/MajiongEmpire/MajiongEmpire.html',
                nodes: []
              }
            ]
          }
        ]
      }
    },

    methods: {
      handleHover (hoverId) {
        if (hoverId) {
          this.hoverId = hoverId
          // console.log(this.hoverId)
        }
      },
      handleHoverOut (hoverId) {
        if (hoverId) {
          this.hoverId = ''
        }
      },
      handleMenuClick (item) {
        if (item) {
          if (item.nodes && item.nodes.length) {
            // 拥有子目录，点击不做处理
          } else if (item.url) {
            this.goRoute(item)
          }
        }
      },
      goRoute (item) {
        if (item && item.url) {
          this.$router.push(item.url)
        }
      },
      goUrl (item) {
        if (item && item.targetUrl) {
          window.location.href = item.targetUrl
        }
      }
    },

    mounted () {
      // console.log(this.$route)
    }
  }
</script>

<style lang="less" scoped>
  .bed-home {
    position: relative;
    height: 100%;
    .bed-menu {
      position: absolute;
      left: 0;
      top: 0;
      width: 200px;
      height: 100%;
      border-right: 1px solid #eaeaea;
      box-shadow: -1px 0 4px 1px inset rgba(190, 190, 190, .4);
      .box-menu {
        .box-menu-item {
          font-weight: 500;
          cursor: pointer;
          .menu-item {
            position: relative;
            height: 34px;
            line-height: 34px;
            padding-left: 12px;
            padding-right: 24px;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            .btn {
              position: absolute;
              right: 4px;
              top: 0;
              color: #000;
              font-size: 12px;
            }
            .btn:hover {
              color: rgb(47, 130, 218);
              font-weight: bold;
            }
          }
          .sub-menu {
            position: relative;
            height: 30px;
            line-height: 30px;
            padding: 0 24px;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            .btn {
              position: absolute;
              right: 4px;
              top: 0;
              font-size: 12px;
            }
            .btn:hover {
              color: rgb(47, 130, 218);
              font-weight: bold;
            }
          }
          .menu-item:hover, .sub-menu:hover {
            background-color: #f0f0f0;
          }
          .active {
            border-left: 2px solid rgb(66, 66, 66);
            color: rgb(47, 130, 218);
            background-color: #e7e7e7;
          }
        }
      }
    }
    .bed-content {
      margin-left: 201px;
    }
  }
</style>



